@import url(https://fonts.googleapis.com/css2?family=Montserrat+Alternates:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,200;0,400;1,100&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

body {
  background-color: lightblue;
  color: black; }

header {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  border-bottom: 2px solid white;
  padding: 25px 0; }
  header .logo {
    width: 70px;
    height: 70px;
    background-image: url(../static/weather.png);
    background-repeat: no-repeat;
    background-position: center; }
  header h1 {
    font-family: 'Poppins'; }
  header .search input {
    background-color: lightblue;
    padding: 10px 12px;
    border-radius: 5px;
    text-transform: uppercase;
    outline: none; }
  header .search button {
    cursor: pointer;
    color: black;
    padding: 8px 12px;
    font-size: 1rem;
    text-transform: uppercase;
    margin-left: 10px; }

.response {
  width: 90%;
  min-height: 150px;
  margin: 25px auto 0 auto;
  background: linear-gradient(to right bottom, rgba(255, 255, 255, 0.7), rgba(255, 255, 255, 0.3));
  backdrop-filter: blur(0.1rem);
  display: flex;
  position: relative;
  flex-direction: column;
  align-items: center;
  justify-content: center; }
  .response .units {
    position: absolute;
    top: 5px;
    right: 5px;
    padding: 5px;
    font-size: 1.5rem;
    background-color: rgba(0, 0, 0, 0.5);
    color: black;
    border-radius: 5px;
    outline: none;
    cursor: pointer; }
  .response .units > .active {
    color: white;
    text-shadow: 2px 2px 8px black; }
  .response .details {
    display: flex; }
    .response .details .temp {
      font-size: 5rem;
      margin: 0 10px; }
    .response .details .more {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center; }
      .response .details .more .icon {
        width: 40px;
        height: 40px;
        margin: 0 10px; }
      .response .details .more .unit {
        font-size: 2rem; }

.alert {
  display: none;
  width: 90%;
  margin: 0 auto;
  height: 25px;
  text-align: center;
  background-color: #ec4d4d;
  text-transform: uppercase;
  color: black;
  line-height: 25px; }

.forecast {
  width: 90%;
  margin: auto; }
  .forecast table {
    margin: 25px 0;
    width: 100%;
    border-collapse: collapse;
    text-align: center;
    text-transform: uppercase; }
    .forecast table tbody tr th {
      padding: 10px 0; }
  .forecast tr {
    border-bottom: 1px solid black; }

